
#header-nav {
  background-color: #0095DA;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  width: 100%;
  height: 2rem;
}
.header-back {
  color: #fff;
  width: 1.5rem;
  height: 100%;
  line-height: 2rem;
  text-align: center;
}
.header-back:after {
    content: " ";
    display: inline-block;
    height: 0.4rem;
    width: 0.4rem;
    border-width: 2px 2px 0 0;
    border-color: #fff;
    border-style: solid;
    -webkit-transform: matrix(-0.71, -0.71, 0.71, -0.71, 0, 0);
            transform: matrix(-0.71, -0.71, 0.71, -0.71, 0, 0);
    position: relative;
    top: -2px;
}
.header-title {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 50%;
  color: #fff;
  text-align: center;
}
.header-title__text {
  font-size: 0.8rem;
  font-color: #fff;
  color: #fff;
  text-align: center;
  font-weight: bold;
}



